import Common from 'common/common.js'
import { ObjOmit } from 'common/important.js'

export default {
    // 
    Init: () => {
        // Bmob.initialize("Application ID", "REST API Key");
        Bmob.initialize(Common.APPLICATION_ID, Common.REST_API_KEY);
    },
    // 
    GetQuery: (tableName) => {
        // 
        return Bmob.Query(tableName);
    },
    // 
    GetListData: (query, pageNo, pageSize) => {
        return new Promise((resolve, reject) => {
            if(pageNo && pageSize){
                // query
                query.count().then(res => {
                    // console.log(`${res}`);
                    let page = {
                        page: pageNo,
                        size: pageSize,
                        count: res,
                        pages: Math.ceil(res.length / pageSize)
                    };

                    // createdAt
                    query.order("-createdAt");
                    // 10
                    query.limit(pageSize);
                    // 
                    query.skip((pageNo - 1) * pageSize);

                    query.find().then(res => resolve({ code: 200, data: res, page })).catch(err => reject(err));
                });
            }
            else{
                query.find().then(res => resolve({ code: 200, data: res })).catch(err => reject(err));
            }
        });
    },
    // 
    FindOneData:(query) => {
        return new Promise((resolve, reject) => {
            query.find().then(res => resolve({ code: 200, data: res[0] })).catch(err => reject(err));
        });
    },
    // 
    GetOne: (tableName, objectId) => {
        let query = Bmob.Query(tableName);
        return new Promise((resolve, reject) => {
            query.get(objectId).then(res => resolve({ code: 200, data: res })).catch(err => reject(err));
        });
    },
    // 
    AddOne: (tableName, params) => {
        let query = Bmob.Query(tableName);
        return new Promise((resolve, reject) => {
            // set
            for(let i in params){
                query.set(i, params[i]);
            }
            query.save().then(() => resolve({ code: 200, msg: '' })).catch(err => reject(err))
        });
    },
    // 
    DelOne: (tableName, objectId) => {
        let query = Bmob.Query(tableName);
        // 
        return new Promise((resolve, reject) => {
            query.get(objectId).then(res => {
                res.destroy().then(() => resolve({ code: 200, msg: '' })).catch(err => reject(err))
            }).catch(() => console.log('objectId'))
        });
    },
    // 
    EditOne: (tableName, objectId, params) => {
        let query = Bmob.Query(tableName);
        // objectId
        ObjOmit(params, ['objectId', 'createdAt', 'updatedAt']);
        // 
        return new Promise((resolve, reject) => {
            query.get(objectId).then(res => {
                // set
                for(let i in params){
                    res.set(i, params[i]);
                }
                res.save().then(() => resolve({ code: 200, msg: '' })).catch(err => reject(err))
            }).catch(() => resolve({ code: 404, msg: '' }))
        });
    },
    // 
    DelMore: (tableName, objectIds) => {
        let query = Bmob.Query(tableName);
        //     
        query.containedIn('objectId', objectIds);
        // 
        return new Promise((resolve, reject) => {
            query.find().then(todos => {
                todos.destroyAll().then(() => resolve({ code: 200, msg: '' })).catch(err => reject(err))
            }).catch(() => resolve({ code: 404, msg: '' }))
        });
    },
    // 
    EditMore: (tableName, objectIds, params) => {
        let query = Bmob.Query(tableName);        
        query.containedIn('objectId', objectIds);
        // 
        return new Promise((resolve, reject) => {
            query.find().then(todos => {
                // set
                for(let i in params){
                    todos.set(i, params[i]);
                }
                todos.saveAll().then(() => resolve({ code: 200, msg: '' })).catch(err => reject(err))
            }).catch(() => resolve({ code: 404, msg: '' }))
        });
    }
}


