<template>
    <div class="g-content">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <Form ref="infoForm" :model="infoForm" :rules="validate" :label-width="100">
                <Form-item label="" prop="title">
                    <Input v-model="infoForm.title" placeholder=""></Input>
                </Form-item>
                <Form-item label="" prop="tags">
                    <Select v-model="infoForm.tags" multiple placeholder="()">
                        <Option v-for="item in tagList" :value="item.name" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </Form-item>
                <Form-item label="" prop="content">
                    <!-- - -->
                    <Tinymce v-model="infoForm.content" height="500" />
                </Form-item>
                <!--  -->
                <div class="m-operation">
                    <Button v-if="pageType == 'edit'" class="fr" type="primary" @click="submit('infoForm')"></Button>
                    <Button v-else class="fr" type="primary" @click="submit('infoForm')"></Button>
                    <Button class="fr" type="ghost" @click="$router.go(-1)"></Button>
                    <div class="clearfix"></div>
                </div>
            </Form>
        </div>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
    //
	import Tinymce from 'components/Common/Tinymce'
    // JS
    import { GetUrlQuery } from 'common/important.js'
    // Api
    import Api from 'api/article_list.js'
    // Json
    import JsonData from 'mock/data.json'
    export default {
        components: { Loading, Tinymce },
        data() {
            return {
                // 
                pageLoading: false,
                // 
                pageType: 'add',
                // 
                tagList: [],
                // id
                articleId: '',
                // 
                infoForm: {
                    title: '',
                    tags: [],
                    content: '',
                },
                // 
                validate: {
                    title: [{ required: true, message: '', trigger: 'blur'}],
                    content: [{ required: true, message: '', trigger: 'blur'}]
                },
            }
        },
        created() {
            let id = GetUrlQuery('id');
            // 
            this.tagList = JsonData.articleTags;
            // 
            if(id){
                // 
                this.articleId = id;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/Examples/ArticleList' },
                    { name: '', path: '' }
                ]);
                // 
                this.getDetail();
                this.pageType = 'edit';
            }
            else{
                this.pageLoading = false;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/Examples/ArticleList' },
                    { name: '', path: '' }
                ]);
            }
        },
        methods: {
            // 
            submit(form) {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        // 
                        this.pageLoading = true;
                        
                        if(this.pageType == 'add'){
                            // 
                            Api.AddArticle(this.infoForm)
                            .then(res => {
                                // 
                                this.pageLoading = false;
                                if(res.code == 200){
                                    this.$Message.success({
                                        content: '!',
                                        onClose: () => {
                                            // 
                                            this.$router.push({ name: 'ArticleList' });
                                        }
                                    }); 
                                }
                                else console.log(res);
                            })
                            .catch(err => this.$Message.warning(err.message))
                        }
                        else{
                            // 
                            Api.EditArticle(this.infoForm, this.articleId)
                            .then(res => {
                                // 
                                this.pageLoading = false;
                                if(res.code == 200){
                                    this.$Message.success('!');
                                    this.getDetail();
                                }
                                else this.$Message.warning(res.msg);
                            })
                            .catch(err => console.log(err))
                        }
                    }
                    else this.$Message.error('');
                })    
            },
            // 
            getDetail(){
                this.pageLoading = true;
                Api.GetArtcDetail(this.articleId)
                .then(res => {                    
                    // 
                    this.pageLoading = false;
                    const result = res.data;                    
                    if(res.code == 200){
                        // 
                        this.infoForm = result;
                    }
                    else this.$Message.warning(res.msg);
                })
                .catch(err => console.log(err))
            }
        }
    }
</script>

<style lang="less">
    @import "../../../assets/less/color";
    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }
</style>


