import Common from 'common/common.js'
import axios from 'axios'

export default {
	data() {
		return{
            // 
            format: ['image/jpg', 'image/jpeg', 'image/png'],
            // finished
            uploadStatus: '',
            // finished
            uploadStatus: '',
            // 
            showProgress: false,
            // 
            loading: false,
            // 
            showModal: false,
            // 
            showMask: false,
            // 
            percentage: 0,
            // 
            showImgUrl: '',
            // 
            showUploadBtn: true
        }
    },
    created(){},
	methods: {
		// 
        viewImage(){
            this.showModal = true;
        },
        // 
        uploadClick(){
            // 
            this.$refs.imgFile.click();
        },
        // Bomb
        uploadToBomb(file){
            // 
            let progress = setInterval(() => {
                if(this.percentage == 90) clearInterval(progress);
                this.percentage += 10;
            },100);
            
            return new Promise((resolve, reject) => {
                file.save().then((res) => {                    
                    // 
                    this.progressHide();
                    this.percentage = 100;
                    clearInterval(progress);
    
                    this.showUploadBtn = false;                    
                    this.$Notice.success({ title: '!' });

                    resolve(res);
                }, (err) =>  {
                    this.errorTip(progress);
                    reject(err);
                });
            });
        },
        // 
        selectFile(){
            // 
            const fileList = this.$refs.imgFile.files;
            if(fileList.length > 0){
                let fileName = fileList[0].name;
                // kb
                let fileSize = Math.floor(fileList[0].size / 1024);

                let maxSize = this.fileSize;                
                let overHint = maxSize + 'kb';
                // 
                if(fileSize > maxSize){
                    if(maxSize > 1024) overHint = Math.floor(maxSize/1024) + 'M';
                    this.$Notice.warning({
                        title: '',
                        desc: ' ' + fileName + '  ' + overHint
                    });
                    this.progressHide();
                    return false;
                }
                this.uploadFile(fileList[0]);
            }
            else console.log('');
        },
        // 
        progressShow(){
            this.loading = true;
            this.uploadStatus = '';
            this.percentage = 0;
            this.showProgress = true;
        },
        // 
        progressHide(){
            this.loading = false;
            this.uploadStatus = 'finished';
            this.showProgress = false;
        },
        // 
        notFoundPic(event){
            Common.SetDefaultPic(event, 1);
        },
        // 
        errorTip(progress){
            // 
            this.progressHide();                 
            clearInterval(progress);
            this.$Notice.error({ title: '' });
        }
	}
}


